.service {
	.general_heading_articles {
		background-color: $light_accent_color;
		float: left;
		width: 100%;
		padding: 22px 40px;
		margin-bottom: 50px;
		h2 {
			margin-top: 0px;
			margin-bottom: 0px;
			color: #fff;
			text-transform: uppercase;
			font-size: 18px;
		}
	}
	.content {
		float: left;
		.general_heading {
			background-image: url('../img/grey_line_pattern.png');
			padding-left: 0;
			margin-bottom: 30px;
			.heading {
				font-size: 18px;
				font-weight: bold;
				color: $orange;
				text-transform: uppercase;
				margin: 0;
				padding-bottom: 1px;
				padding-right: 3%;
				background-color: #fff;
				display: inline-block;
				padding-left: 0;
			}
		}
	}
	.short_description {
		overflow: hidden;
		margin-bottom: 60px;
		.icon {
            max-width: 128px;
            max-height: 128px;
            min-height: 128px;
            min-width: 128px;
            background-image: url('data:image/png;base64,');
            float: left;
            margin-right: 25px;
		}
		.text {
			padding: 30px 20px;
			text-align: justify;
			font-size: 14px;
			float: left;
			width: 85.5%;
			background-color: $peach_orange;
		}
	}
	.full_description_wrap {
		background-color: #eee8d6;
		background-image: url(../img/background_service.png);
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center center;
		&.law {
			background-size: cover;
		}
	}
	.marketing {
		padding-top: 30px;
		font-size: 14px;
		.part {
			margin-bottom: 45px;
			.icon_wrap {
				margin-top: 7%;
				&.mt10 {
					margin-top: 10%;
				}
				&.mt13 {
					margin-top: 13%;
				}
			}
			.icon {
				max-width: 128px;
				max-height: 128px;
				min-height: 128px;
				min-width: 128px;
				background-image: url('data:image/png;base64,');
				display: table;
				margin: auto;
			}
			.text {
				text-align: justify;
				.heading {
					font-size: 16px;
					text-transform: uppercase;
					font-weight: bold;
					margin-bottom: 25px;
				}
				.bold {
					font-weight: bold;
				}
			}
		}
	}
