	.law_alt {
		padding-top: 30px;
		.nav-tabs {
			border: none;
			.content {
				padding: 18px 10px 20px 20px;
			    overflow: hidden;
			    position: relative;
			}
			li {
				a {
					padding: 10px 22px;
					color: $text_color;
					font-size: 18px;
					text-transform: uppercase;
					line-height: 1;
					margin-right: 25px;
					text-decoration: none;
					width: 100%;
					&:hover {
						cursor: pointer;
	    				background-color: $light_accent_color;
	    				text-decoration: none;
	    				color: #fff;
	    				text-transform: uppercase;
					}
				}
				&.active {
					a {
						cursor: pointer;
	    				background-color: $light_accent_color;
	    				text-decoration: none;
	    				color: #fff;
	    				text-transform: uppercase;
					}
				}
			}
		}
		.tab-pane {
			padding-top: 40px;
			margin-bottom: 45px;
			.part {
				margin-bottom: 45px;
			}
			.icon {
				max-width: 128px;
				max-height: 128px;
				min-height: 128px;
				min-width: 128px;
				background-image: url('data:image/png;base64,');
				display: table;
				margin: auto;
			}
			.text {
				text-align: justify;
				.part_heading {
					font-size: 16px;
					text-transform: uppercase;
					font-weight: bold;
					margin-bottom: 25px;
				}
			}
		}
	}
	.social {
		padding-top: 30px;
		font-size: 14px;
		.part {
			margin-bottom: 45px;
			.icon_wrap {
				margin-top: 14%;
				&.mt17 {
					margin-top: 17%;
				}
			}
			.icon {
				max-width: 128px;
				max-height: 128px;
				min-height: 128px;
				min-width: 128px;
				background-image: url('data:image/png;base64,');
				display: table;
				margin: auto;
			}
			.text {
				text-align: justify;
				.heading {
					font-size: 16px;
					text-transform: uppercase;
					font-weight: bold;
					margin-bottom: 25px;
				}
				.no_dot {
					list-style: none;
				}
				.bold {
					font-weight: bold;
				}
			}
		}
	}
}